import "./AddUser.css";
import { useState } from "react";
import ButtonComp from "../Buttons/ButtonComp";
import add from "../../assets/icons/add-user.png";
import CloseButton from "../Buttons/CloseButton";

const AddUser = (props) => {
  const {
    setShowDeposit,
    setShowWithdraw,
    setShowSendMoney,
    setShowAddUser,
    setShowRemoveUser,
    setUsersInfo,
    usersInfo,
    setLoading,
    loggedInEmployee,
  } = props;

  const [userName, setUserName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [amount, setAmount] = useState("");

  const [isErrorShow, setShowError] = useState(false);
  const [isInvalidPassword, setPasswordError] = useState(false);
  const [isInvalidName, setNameError] = useState(false);

  const handleAddUser = (event) => {
    setUserName(event.target.value);
  };

  const handleSetEmail = (event) => {
    setEmail(event.target.value);
  };

  const handleSetPassword = (event) => {
    setPassword(event.target.value);

    if (event.target.value === "") {
      setPasswordError(false);
    }
  };

  const handleSetAmount = (event) => {
    setAmount(event.target.value);
  };

  const handleNewUser = (newUser) => {
    setUsersInfo((prevUsers) => [...prevUsers, newUser]);
  };

  const addNewUser = (event) => {
    event.preventDefault();

    const userNameChecker = /^\d/.test(userName);
    if (userNameChecker) {
      setNameError(true);
      setShowError(false);
      setPasswordError(false);
      return;
    }

    if (password.length < 6) {
      setPasswordError(true);
      setNameError(false);
      setShowError(false);
      return;
    }

    const prevUser = usersInfo.find(
      (user) => user.Name === userName || user.Email === email
    );

    if (!prevUser) {
      const formatNumberWithCommas = (num) => {
        return parseFloat(num.toLocaleString());
      };

      const newUser = {
        Name: userName,
        Email: email,
        Password: password,
        Balance: formatNumberWithCommas(amount),
        Handled_by: loggedInEmployee.Name,
        Id: crypto.randomUUID(),
      };

      handleNewUser(newUser);
      setUserName("");
      setEmail("");
      setPassword("");
      setAmount("");
      setShowAddUser(false);
      setLoading(true);
    } else {
      setShowError(true);
      setNameError(false);
      setPasswordError(false);
    }
  };

  return (
    <div className="add-user-div">
      <form className="add-user-form" onSubmit={addNewUser}>
        <CloseButton
          setShowDeposit={setShowDeposit}
          setShowWithdraw={setShowWithdraw}
          setShowSendMoney={setShowSendMoney}
          setShowAddUser={setShowAddUser}
          setShowRemoveUser={setShowRemoveUser}
        />

        <h3 className="add-user-header">ADD USER</h3>

        <label className="add-user-label">NAME:</label>

        <input
          type="text"
          className="add-user-input"
          id="name"
          value={userName}
          onChange={handleAddUser}
          placeholder="Enter name"
          required
        />

        <label className="add-user-label">EMAIL:</label>

        <input
          type="email"
          className="add-user-input"
          id="email"
          value={email}
          onChange={handleSetEmail}
          placeholder="Enter email"
          required
        />

        <label className="add-user-label">PASSWORD:</label>

        <input
          type="password"
          className="add-user-input"
          id="password"
          value={password}
          onChange={handleSetPassword}
          placeholder="Enter password (min. 6 chars)"
          required
        />

        <label className="add-user-label">DEPOSIT AMOUNT:</label>

        <input
          type="number"
          className="amount-input"
          id="amount"
          value={amount}
          onChange={handleSetAmount}
          placeholder="Enter amount"
          min="0"
          step="0.01"
          required
        />

        {isErrorShow && (
          <div className="user-exist-error">
            <p>USER ALREADY EXIST</p>
          </div>
        )}

        {isInvalidPassword && (
          <div className="invalid-password-error">
            <p>INVALID PASSWORD</p>
          </div>
        )}

        {isInvalidName && (
          <div className="invalid-name-error">
            <p>INVALID NAME</p>
          </div>
        )}

        <div className="add-button">
          <ButtonComp iconSrc={add} label="Add User" type="submit" />
        </div>
      </form>
    </div>
  );
};

export default AddUser;
